import './RollCamera.css';

const avp = Autodesk.Viewing.Private;


/**
 * Provides UI controls to perform rotation of camera view.
 * 
 * The extension id is: `Autodesk.BIM360.RollCamera`
 * 
 * @example
 *   viewer.loadExtension('Autodesk.BIM360.RollCamera')
 *  
 * @memberof Autodesk.Viewing.Extensions
 * @alias Autodesk.Viewing.Extensions.RollCameraExtension
 * @see {@link Autodesk.Viewing.Extension} for common inherited methods.
 * @class
 */
export default class RollCameraExtension extends Autodesk.Viewing.Extension {
    constructor(viewer, options = {}) {
        super(viewer, options);
        this.viewer = viewer;
        this.options = options;
        this.enabled = false;
        this.update = this.update.bind(this);
    }

    /**
     * Load the roll camera extension.
     *
     * @alias Autodesk.Viewing.Extensions.RollCameraExtension#load
     */
    load() {
        this.viewer.addEventListener(Autodesk.Viewing.MODEL_ADDED_EVENT, this.update);
        this.viewer.addEventListener(Autodesk.Viewing.MODEL_REMOVED_EVENT, this.update);

        return true;
    }

    /**
     * Unload the roll camera extension.
     *
     * @alias Autodesk.Viewing.Extensions.RollCameraExtension#unload
     */
    unload() {
        this.viewer.removeEventListener(Autodesk.Viewing.MODEL_ADDED_EVENT, this.update);
        this.viewer.removeEventListener(Autodesk.Viewing.MODEL_REMOVED_EVENT, this.update);

        this.destroyUI();

        this.viewer = null;
        this.options = null;

        return true;
    }

    update() {
        const models = this.viewer.impl.modelQueue().getModels();

        if (models.length !== 1) {
            this.enabled = false;
        } else {
            const model = models[0];
            this.enabled = (this.viewer.impl.is2d && (model.isLeaflet() || model.isPdf(true)));
        }

        if (this.enabled) {
            this.createUI();
        } else {
            this.destroyUI();
        }

    }

    /**
     * Invoked by the viewer when the toolbar UI is available.
     *
     * @param {Autodesk.Viewing.UI.ToolBar} toolbar - toolbar instance.
     *
     * @alias Autodesk.Viewing.Extensions.RollCameraExtension#onToolbarCreated
     */
    onToolbarCreated() {
        this.update();
    }

    /**
     * Roll the camera 90 degrees.
     * 
     * @param {boolean} clockwise - True to rotate clockwise, false to rotate counter clockwise.
     * @alias Autodesk.Viewing.Extensions.RollCameraExtension#roll
     */
    async roll(clockwise = false) {
        await Autodesk.Viewing.EventUtils.waitUntilTransitionEnded(this.viewer);

        const { autocam } = this.viewer;

        const destination = {
            center: autocam.center.clone(),
            position: autocam.camera.position.clone(),
            pivot: autocam.camera.pivot.clone(),
            fov: autocam.camera.fov,
            worldUp: autocam.sceneUpDirection.clone(),
            isOrtho: autocam.camera.isPerspective === false
        };

        const dir = autocam.center.clone().sub(autocam.camera.position).normalize();

        if (clockwise) {
            destination.up = autocam.camera.up.clone().cross(dir);
        } else {
            destination.up = autocam.camera.up.clone().multiplyScalar(-1);
            destination.up.cross(dir);
        }

        destination.up.normalize();

        // add global position/center/pivot - which are stable under globalOffset changes
        autocam.addGlobalPositions(destination);

        autocam.elapsedTime = 0.0;
        autocam.animateTransition(destination);
    }

    /**
     * @private
     */
    createUI() {
        const toolbar = this.viewer.getToolbar && this.viewer.getToolbar();

        if (!toolbar || this.rollCameraBtn) {
            return;
        }

        const toolbarName = this.viewer.config.hideSettingsToolbar ? Autodesk.Viewing.TOOLBAR.NAVTOOLSID : Autodesk.Viewing.TOOLBAR.SETTINGSTOOLSID;  
        const toolbarGroup = toolbar.getControl(toolbarName);

        if (!toolbarGroup) {
            return;
        }

        // Create the button first.
        this.rollCameraBtn = new Autodesk.Viewing.UI.Button('toolbar-rool-camera');
        this.rollCameraBtn.setToolTip(Autodesk.Viewing.i18n.translate('Rotate page'));
        this.rollCameraBtn.setIcon('bimviewericon-roll');

        this.rollCameraBtn.onClick = () => {
            this.roll(this.options.clockwise);
            avp.analytics.track('viewer.roll_camera', {
                from: 'UI',
            });
        };

        const index = this.viewer.config.hideSettingsToolbar ? undefined : toolbarGroup.indexOf(toolbarGroup.getControl('toolbar-settingsTool'));
        toolbarGroup.addControl(this.rollCameraBtn, { index });
    }

    /**
     * @private
     */
    destroyUI() {
        if (!this.rollCameraBtn) {
            return;
        }

        const toolbar = this.viewer.getToolbar && this.viewer.getToolbar();

        if (!toolbar) {
            return;
        }

        this.rollCameraBtn.removeFromParent();
        this.rollCameraBtn = null;
    }
}

RollCameraExtension.extensionName = 'Autodesk.BIM360.RollCamera';

Autodesk.Viewing.theExtensionManager.registerExtension(RollCameraExtension.extensionName, RollCameraExtension);

